<template>
    <div class="content-bg" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
      <div class="top-title">User-Agent信息</div>
      <div style="padding: 0 5px;"><v-basedate class="from-wapper" type="datetimerange" busName="userAgentInfoDateBus2"></v-basedate></div>
      <div class="item-wapper">
        <div class="item-title">业务访问用户统计-操作系统</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.systemChartData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">业务访问用户统计-操作系统</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.systemChartData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">业务访问用户统计-浏览器</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.browserChartData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">业务访问用户统计-浏览器</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.browserChartData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vBasedate from '../common/baseDate'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "userAgentInfo2",
        data() {
            return {
                fullscreenLoading:false,
                interTime:'',
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:3600,
                //操作系统 柱状图数据
                systemChartData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'操作系统',
                        yAxisName:'访问次数/次',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]

                },
                //操作系统 饼图数据
                systemChartData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]

                },
                //浏览器
                browserChartData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'浏览器',
                        yAxisName:'访问次数/次',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]

                },
                browserChartData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]

                },
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('userAgentInfoDateBus2',(arr)=>{
              let paramObj = {
                starttime:arr[0],
                endtime:arr[1]
              }
              /*获取业务系统统计- 浏览器*/
              this.getBrowserData(paramObj);
              /*获取业务系统统计- 系统*/
              this.getSystemData(paramObj);
            })
        },
        mounted(){},
        methods:{
            /*获取业务系统统计- 浏览器*/
            getBrowserData(paramObj){
              this.fullscreenLoading = true;
                this.$nextTick(()=>{
                    this.$post('/flow/getUserAgentBrowserGroupByTime.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                            this.browserChartData.xAxisArr = xns1;
                            this.browserChartData.yAxisArr = yvs1;
                            this.browserChartData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
            /*获取业务系统统计- 系统*/
            getSystemData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getUserAgentOSGroupByTime.do',paramObj)
                        .then(res=>{
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                            this.systemChartData.xAxisArr = xns1;
                            this.systemChartData.yAxisArr = yvs1;
                            this.systemChartData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{

                        })
                })
            },
        },
        components:{
            vBasedate,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .from-wapper{
    margin-top: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



